import { ref } from 'vue'
import { defineStore } from 'pinia'
import type { IUser } from '@/shared/types/user/IUser'

export const useUserStore = defineStore('user', () => {
  const user = ref<IUser | null>(null)
  const storeUsers = ref<IUser[]>([])

  function storeSetUser(u: IUser | null) {
    user.value = u
  }

  function storeSetUsers(u: IUser[]) {
    storeUsers.value = u
  }

  function storeAddUser(u: IUser) {
    storeUsers.value.push(u)
  }

  function storeRemoveUser(u: IUser) {
    const index = storeUsers.value.findIndex((usr) => usr.id === u.id)
    if (index !== -1) {
      storeUsers.value.splice(index, 1)
    }
  }

  function storeUpdateUser(u: IUser) {
    const index = storeUsers.value.findIndex((usr) => usr.id === u.id)
    storeUsers.value[index] = u
  }

  function storeResetUsers() {
    storeUsers.value = []
  }

  return {
    storeSetUser,
    user,
    storeSetUsers,
    storeUsers,
    storeAddUser,
    storeUpdateUser,
    storeResetUsers,
    storeRemoveUser,
  }
})
