import type { z } from 'zod'
import { StatusCodes } from 'http-status-codes'

import {
  API_HOST,
  API_URL,
  CSRF_ENDPOINT,
  IS_DEV,
  IS_PHP_STORM_XDEBUG_ENABLED,
} from '@/shared/config'
import type { AnyObject } from '@/shared/lib/utils'

import { FetchError, UnauthorizedError } from './errors'

function getCookie(name: string) {
  const matches = document.cookie.match(new RegExp(
    `(?:^|; )${name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1')}=([^;]*)`,
  ))
  return matches ? decodeURIComponent(matches[1]) : undefined
}

export class RestClient {
  get<T>(url: string, schema?: z.ZodType<T>, params?: RequestInit): Promise<T> {
    return this.#request(url, schema, {
      method: 'GET',
      ...params,
    })
  }

  post<T>(
    url: string,
    payload?: AnyObject,
    schema?: z.ZodType<T>,
    params?: RequestInit,
  ): Promise<T> {
    return this.#request(url, schema, {
      method: 'POST',
      body: JSON.stringify(payload),
      ...params,
    })
  }

  put<T>(
    url: string,
    payload?: AnyObject,
    schema?: z.ZodType<T>,
    params?: RequestInit,
  ): Promise<T> {
    return this.post(url, payload, schema, {
      method: 'PUT',
      ...params,
    })
  }

  patch<T>(
    url: string,
    payload?: AnyObject,
    schema?: z.ZodType<T>,
    params?: RequestInit,
  ): Promise<T> {
    return this.post(url, payload, schema, {
      method: 'PATCH',
      ...params,
    })
  }

  delete<T>(
    url: string,
    schema?: z.ZodType<T>,
    params?: RequestInit,
  ): Promise<T> {
    return this.#request(url, schema, {
      method: 'DELETE',
      ...params,
    })
  }

  xhrPost<T>(
    url: string,
    payload?: XMLHttpRequestBodyInit,
    schema?: z.ZodType<T>,
    onProgress?: (v: number) => void,
  ): Promise<T> {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest()

      xhr.open('POST', this.#formatEndpoint(url))
      xhr.setRequestHeader('Accept', 'application/json')

      xhr.upload.onprogress = (event) => {
        if (onProgress) {
          onProgress(event.loaded / event.total)
        }
      }

      xhr.onload = () => {
        const data = JSON.parse(xhr.response)

        if (schema) {
          return resolve(schema.parse(data))
        }

        return resolve(data)
      }

      xhr.onerror = () => reject(new Error(xhr.statusText))

      xhr.send(payload)
    })
  }

  async #request<T>(
    url: string,
    schema?: z.ZodType<T>,
    params?: RequestInit,
  ): Promise<T> {
    const endpoint = this.#formatEndpoint(url)

    const headers: HeadersInit = {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
      'X-XSRF-Token': getCookie('XSRF-TOKEN') ?? '',
    }

    if (IS_PHP_STORM_XDEBUG_ENABLED) {
      headers['XDEBUG_SESSION_START'] = 'PHPSTORM'
    }

    const response = await fetch(endpoint, {
      credentials: 'include',
      ...params,
      headers: {
        ...headers,
        ...params?.headers,
      },
    })

    if (response.status === StatusCodes.UNAUTHORIZED) {
      throw new UnauthorizedError()
    }

    if (response.status === StatusCodes.INSUFFICIENT_SPACE_ON_RESOURCE) {
      await this.get(API_HOST + CSRF_ENDPOINT)

      if (IS_DEV) {
        console.log('Fetching fresh CSRF token and retrying original request.')
      }

      return this.#request(url, schema, params)
    }

    return this.#handleResponse(response, schema)
  }

  #formatEndpoint(url: string) {
    if (!url.startsWith('http')) {
      return `${API_URL}${url}`
    }

    return url
  }

  async #handleResponse<T>(response: Response, schema?: z.ZodType<T>) {
    if (response.ok) {
      if (response.status === StatusCodes.NO_CONTENT) {
        return null
      }

      const data = await response.json()

      if (schema) {
        return schema.parse(data)
      }

      return data
    }

    throw new FetchError(response)
  }
}
