import type { MaybeRef } from 'vue'
import { and } from '@vueuse/math'
import { type ZodType, z } from 'zod'
import type { HttpHandler } from 'msw'

import { API_URL } from '@/shared/config'
import { type ParametersExceptFirst, objectEnsure, omit } from '@/shared/lib/utils'

import { RestClient } from './RestClient'

export * from './query-client'

export const client = new RestClient()

// TODO: Not used currently, decide when to remove
export function parseDataAgainstSchema<T extends ZodType<unknown>>(
  schema: T,
  data: z.infer<z.AnyZodObject>,
): z.infer<T> {
  return schema.parse(data)
}

// Query keys
type KeyParam<T = number> = MaybeRef<T | null | undefined>

export type ToKeyParams<T extends Record<string, unknown>> = {
  [K in keyof T]: KeyParam<T[K]>
}

type Key = { entity: string; scope: string }

export type Keys = Record<string, [Key] | ((...args: any) => [Key])>

export const trimKey = <T extends Key>(key: [T] | readonly[T]) => objectEnsure(
  omit(key[0], 'entity', 'scope'),
)

export const paramsAnd = (params: Record<string, KeyParam<unknown>>) => and(...Object.values(params))

// Endpoints
type EndpointAndSchema = {
  url: string | ((...args: any) => string)
  method: 'get' | 'post' | 'put' | 'patch' | 'delete'
  schema?: z.ZodTypeAny
}

export type ApiEndpointsAndSchemas = Record<string, EndpointAndSchema | (() => EndpointAndSchema)>

// Mocks
export type Handlers<
  TEndpoints extends ApiEndpointsAndSchemas, TPrefix extends string,
 > = {
   [K in keyof TEndpoints as `${TPrefix}${Capitalize<string & K>}`]: HttpHandler
 }

export const mockUrl = (path: string) => `${API_URL}${path}`

export const mockUrlParam = (param: `:${string}`) => param as any

// Misc
export const SuccessfulResponse = z.object({ status: z.literal('success') })
export const SuccessfulResponseMock = { status: 'success' } satisfies z.infer<typeof SuccessfulResponse>

export const PaginatedResponse = <T extends z.ZodTypeAny>(schema: T) => z.object({
  data: z.array(schema),
  // TODO: CamelCase
  meta: z.object({ last_page: z.number(), current_page: z.number() }),
})

export function definePaginatedFetcher<
  T,
  A extends any[],
>(fn: (page: number, ...args: A) => Promise<{
  data: T[]
  meta: { last_page: number }
}>) {
  return async (...args: ParametersExceptFirst<typeof fn>) => {
    const result: T[] = []

    let page = 1
    let isFinished = false

    do {
      const { data, meta } = await fn(page, ...args)

      result.push(...data)

      page += 1
      isFinished = page > meta.last_page
    } while (!isFinished)

    return result
  }
}
