import { z } from 'zod'
import { isNumber } from '@antfu/utils'

import type {
  Booking,
  Department,
  Departure,
  OperationalBase,
  Passenger,
  ScheduledDeparture,
  Tour,
  TourBooking,
  User,
  Vehicle,
} from '@/shared/types'
import {
  // BookingPaymentStatus,
  BookingStatus,
  ScheduledDepartureStatus,
} from '@/shared/types'

export const DepartmentSchema = z.object({
  id: z.custom<Department['id']>(isNumber),
  organisationId: z.number(),
  name: z.string(),
  code: z.string().or(z.number()),
  description: z.string().nullable(),
  logoThumbnailSrc: z.string().nullable().optional(),
})

export const OperationalBaseSchema = z.object({
  id: z.custom<OperationalBase['id']>(isNumber),
  name: z.string(),
  latitude: z.string(),
  longitude: z.string(),
  department: DepartmentSchema,
})

export const UserSchema = z.object({
  id: z.custom<User['id']>(isNumber),
  name: z.string(),
  email: z.string().email(),
  contactNumber: z.string(),
  avatar: z.string().url().nullable(),
})

export const VehicleSchema = z.object({
  id: z.custom<Vehicle['id']>(isNumber),
  title: z.string(),
  registrationNumber: z.string(),
  maxCapacity: z.number(),
})

export const TourSchema = z.object({
  id: z.custom<Tour['id']>(isNumber),
  code: z.string(),
  name: z.string(),
  thumbnailUrl: z.string().url().nullable(),
})

export const MainBookingPassengerSchema = z.object({
  id: z.custom<Passenger['id']>(isNumber),
  name: z.string(),
  email: z.string().nullable(),
  phone: z.string().nullable(),
}).nullable()

export const BookingSchema = z.object({
  id: z.custom<Booking['id']>(isNumber),
  code: z.string(),
  externalReference: z.string(),
  mainPassengerBooking: MainBookingPassengerSchema,
  // paymentStatus: z.nativeEnum(BookingPaymentStatus).nullable(),
})

export const ScheduledDepartureIdSchema = z.custom<ScheduledDeparture['id']>(isNumber)

export const ExtraSchema = z.object({
  id: z.number(),
  code: z.string(),
  description: z.string(),
  externalId: z.string(),
  name: z.string(),
})

export const QuestionSchema = z.object({
  id: z.number(),
  answer: z.string(),
  externalId: z.string(),
  label: z.string(),
})

export const PickupPlaceSchema = z.object({
  id: z.number(),
  name: z.string(),
}).nullable()

export const ExtraBookingSchema = z.object({
  extra: ExtraSchema,
  questions: z.array(QuestionSchema).optional(),
  quantity: z.number(),
  id: z.number(),
}).nullable()

export const PassengerBookingSchema = z.object({
  extraBookings: z.array(ExtraBookingSchema).optional(),
}).nullable()

export const TourBookingSchema = z.object({
  id: z.custom<TourBooking['id']>(isNumber),
  scheduledDepartureId: z.lazy(() => ScheduledDepartureIdSchema),
  externalId: z.string(),
  code: z.string(),
  // TODO: YYYY-MM-DD
  date: z.string(),
  booking: BookingSchema,
  status: z.nativeEnum(BookingStatus),
  totalPassengerCount: z.number(),
  arrivedPassengerCount: z.number().nullable(),
  url: z.string().url().nullable(),
  waiverStatus: z.boolean().nullable(),
  pickupPlace: PickupPlaceSchema,
  passengerBookings: z.array(PassengerBookingSchema).optional(),
})

export const DepartureSchema = z.object({
  id: z.custom<Departure['id']>(isNumber),
  time: z.string(),
  durationSeconds: z.number().nullable(),
})

export const ScheduledDepartureSchema = z.object({
  id: ScheduledDepartureIdSchema,
  start: z.string().datetime(),
  end: z.string().datetime(),
  comment: z.string().nullable(),
  totalPassengerCount: z.number(),
  bookingSystemAvailability: z.number().nullable(),
  status: z.nativeEnum(ScheduledDepartureStatus),
  tour: TourSchema,
  departure: DepartureSchema,
  tourBookings: z.array(TourBookingSchema).optional(),
  scheduledUsers: z.array(
    z.object({
      userId: UserSchema.shape.id,
      vehicleId: VehicleSchema.shape.id.nullable(),
    }),
  ),
})
